html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Slab', "Roboto", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

svg {
	vertical-align: text-bottom;
}
.App {
  text-align: center;
  font-size: calc(10px + 2vmin); }

h1 {
  margin: 2vh;
  font-size: calc(10px + 4vmin);
  z-index: 100; }

h2 {
  margin: 2vh;
  font-size: calc(10px + 3vmin);
  z-index: 100; }

h3 {
  font-size: calc(10px + 2vmin);
  margin: 1vh;
  font-weight: 700;
  border-color: black;
  border-left: 3px solid;
  border-right: 3px solid;
  border-bottom: 3px solid;
  border-radius: 8px;
  padding: 10px;
  position: relative;
  min-width: 12vw; }
  h3::after {
    content: "";
    position: absolute;
    right: -3px;
    z-index: 100;
    top: 0;
    width: 7px;
    height: 80%;
    background: #ecf0f1; }
  h3::before {
    content: "";
    position: absolute;
    left: -3px;
    z-index: 100;
    top: 0;
    width: 7px;
    height: 80%;
    background: #ecf0f1; }

h4 {
  font-weight: 700;
  margin: 1vmin; }

h5 {
  font-style: italic;
  font-size: calc(10px + .6vmin);
  margin: 1vmin; }

p {
  line-height: 2;
  font-size: calc(10px + .75vmin); }

button {
  border-radius: 8px;
  background-color: #2c3e50;
  color: white;
  border: 1px solid #2c3e50;
  margin: 1vh;
  padding: 10px;
  font-size: calc(10px + 1vmin);
  transition: all 0.5s ease 0s; }
  button:hover {
    background-color: white;
    color: #2c3e50;
    transition: all 0.5s ease 0s; }
  button:focus, button:active {
    outline: none; }

.light-border::after {
  background: white; }

.light-border::before {
  background: white; }

.medium-border::after {
  background: #2c3e50; }

.medium-border::before {
  background: #2c3e50; }

.title-space {
  margin-top: -3vh;
  margin-bottom: 3vh; }

.picture {
  z-index: 100;
  border-radius: 50%;
  height: 40vmin;
  border: 2px solid #F5F5F5; }

.relative {
  position: relative;
  width: 4vmax;
  margin: 2vmax; }

.link-image {
  z-index: 100;
  height: 2.75vmax;
  transition: all 500ms ease 0ms;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  .link-image:hover, .link-image:focus, .link-image:active {
    transition: all 500ms ease 0ms;
    height: 3.5vmax;
    min-width: 30px;
    min-height: 30px;
    cursor: pointer; }

.linkedin:hover, .linkedin:focus, .linkedin:active {
  background: #2980b9;
  border-radius: 50%; }

.github {
  border-radius: 100%; }
  .github:hover, .github:focus, .github:active {
    border-radius: 18%;
    background: black; }

.fill {
  height: 100vh; }

.logo {
  height: 4vmax;
  margin: 1vw;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1); }
  .logo:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    z-index: 100;
    cursor: pointer; }

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around; }

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.light {
  background-color: white;
  color: black; }

.medium {
  background-color: #ecf0f1;
  color: black; }

.dark {
  background-color: #2c3e50;
  color: white; }

.inner {
  max-width: 1000px;
  margin: 2.5vmin;
  height: auto;
  padding: 2vw;
  text-align: left;
  font-size: calc(10px + 1vmin);
